.bannerslide {
    &-image {
        height: 700px;
        overflow: hidden;
    // 자동 슬라이드

        &>ul{
            width: calc(100% * 4);
            display: flex;
            animation: slidebanner 20s infinite;
            animation-delay: 2s;
            &>li {
                width: calc(100% /4);
                height: 700px;
                &:nth-child(1) { 
                    background: url(../../images/visual1.jpg) no-repeat; 
                    background-size: cover;
                    min-height: 700px;
                }
                &:nth-child(2) { 
                    background: url(../../images/visual2.jpg) no-repeat; 
                    background-size: cover;
                    min-height: 700px;
                }
                &:nth-child(3) { 
                    background: url(../../images/visual3.jpg) no-repeat; 
                    background-size: cover;
                    min-height: 700px;
                }
                &:nth-child(4) { 
                    background: url(../../images/visual4.jpg) no-repeat; 
                    background-size: cover;
                    min-height: 700px;
                }
            }
            @keyframes slidebanner {
                0% {margin-left:0;} 
                10% {margin-left:0;} 
                15% {margin-left:0;} 
                25% {margin-left:-100%;} 
                35% {margin-left:-100%;} 
                40% {margin-left:-100%;} 
                50% {margin-left:-200%;}
                60% {margin-left:-200%;}
                65% {margin-left:-200%;}
                75% {margin-left:-300%;}
                85% {margin-left:-300%;}
                90% {margin-left:-300%;}
                100% {margin-left:0;}
            }
        }


